import "./Fishes.css";

export const Fishes = ({fishes}) => {
    return <div className={"fishes-container"}>
            {Array.from(Array(fishes)).map((_, i) => {
                const LoR = Math.random()>=0.5?1:-1;
                const z = 4+Math.round(Math.random()*6);
                const scale = 0.2+z/20;
                const d = (Math.random()*50)+100;
                const dt = (Math.random()*50)+(Math.random()*10);
                const schools = Math.round(Math.random()*3)+1;
                return (<div className={"fish-school"} key={i} style={{
                            top: `${50+Math.random()*40}%`,
                            left: LoR==1?"-5%": "",
                            right: LoR!=1?"-5%": "",
                            animationName: LoR==1?"swim":"swim-reverse",
                            animationDelay: `${dt}s`,
                            animationDuration: `${d}s`,
                            zIndex: z,
                            opacity: 0.5+z/10, 
                    }}>
                        {Array.from(Array(schools)).map((_, j) => { 
                            return <div key={j} className={"fish"} style={{
                                marginLeft: `${Math.random()*z/2}vh`,
                                marginTop: `${Math.random()*z/2}vh`,
                                width: `${10*scale}px`,
                                height: `${5*scale}px`,
                                transform: LoR==1?"":"rotateY(180deg)",
                            }}/>
                        })}
                    </div>)
            })}
        </div>
};
