import { useState, useEffect, useRef } from "react";
import "./ProjectList.css";

const theLostLanguageGenerator = (str) => { 
    const poolsCons = "bcdfghjklmnpqrstvwxyz".split("");
    const poolsVows = "aeiou".split("");
    return str.split(" ").map((word) => {
        const sglQuote = word.length > 2 ? Math.round(Math.random() * 2) : 0;
        const randQuotes = Array.from(Array(sglQuote)).map(() => {
            const randQ = Math.ceil(Math.random() * word.length) - 1
            return randQ <= 0 ? 0 : randQ;
        })
        return word.split("").map((c, i) => {
            const randVow = Math.ceil(Math.random() * 5) - 1;
            const randCon = Math.ceil(Math.random() * 21) - 1;
            if (randQuotes.includes(i)) return "'"
            const ltr = poolsVows.includes(c.toLowerCase()) ? poolsVows[(randVow <= 0 ? 0 : randVow)] : poolsCons[(randCon <= 0 ? 0 : randCon)];
            return i == 0 ? ltr.toUpperCase() : ltr;
        }).join("")
    }).join(" ")
}

export const ProjectList = () => {
    const projectList = {
        "pokemon": (key) => { 
            return <ProjectPokemon key={key} lostTitle={theLostLanguageGenerator("Pokemon Database")} lostDescription={theLostLanguageGenerator("Database of pokemon retrieved by API calls.")} /> 
        },
        "yugioh": (key) => {
            return <ProjectYugioh key={key} lostTitle={theLostLanguageGenerator("Yugioh Database")} lostDescription={theLostLanguageGenerator("Database of yugioh retrieved by API calls.")} />
        }
    }
    return (
        <div className={"projects-list-container"}>
            {Object.keys(projectList).map((k, i) => {
                return projectList[k](i)
            })}
        </div>
    );
}

const LostLetter = ({ letter, duration }) => {
    const [display, setDisplay] = useState(letter);
    useEffect(() => {
        let timer; 
        timer = setTimeout(() => {
                setDisplay(letter);
            }, duration);
        return () => clearTimeout(timer);
    }, [letter, duration]);
    return <div className={"project-title-letter-container"}>  
        <div className={"lost-language-container"} style={{ margin: letter == " " ? "5px"  : "" }} >
            {display}
        </div>
    </div>
}

const LostWord = ({ lostTitle, decodedWord, hover }) => {
    return lostTitle.split("").map((c, i) => {
        return <LostLetter key={i} letter={hover ? decodedWord[i] : c} duration={i * 20} /> 
    })
}

const ProjectPokemon = ({ lostTitle, lostDescription }) => {
    const [hover, setHover] = useState(false);
    return (
        <div className={"project-container"} 
            onMouseEnter={() => { setHover(true) }}
            onMouseLeave={() => { setHover(false) }}>
            <div className={"pokemon-icon-container"} />
            <div className={"project-text-container"} >
                <div className={"project-title"}>
                    <LostWord lostTitle={lostTitle} decodedWord={"Pokemon Database"} hover={hover} />
                </div>
                <div className={"project-description"}>
                    <LostWord lostTitle={lostDescription} decodedWord={"Database of pokemon retrieved by API calls."} hover={hover} />
                </div>
            </div>
            
        </div>
    )
}


const ProjectYugioh = ({ lostTitle, lostDescription }) => {
    const [hover, setHover] = useState(false);
    return (
        <div className={"project-container"} 
            onMouseEnter={() => { setHover(true) }}
            onMouseLeave={() => { setHover(false) }}>
            <div className={"yugioh-icon-container"} />
            <div className={"project-text-container"} >
                <div className={"project-title"}>
                    <LostWord lostTitle={lostTitle} decodedWord={"Yugioh Database"} hover={hover} />
                </div>
                <div className={"project-description"}>
                    <LostWord lostTitle={lostDescription} decodedWord={"Database of yugioh retrieved by API calls."} hover={hover} />
                </div>
            </div>
            
        </div>
    )
}