import "./ReturnToSurface.css";
import { animateScroll as scroll } from "react-scroll";

export const ReturnToSurface = () => {        
    function returnUp() {
        scroll.scrollTo(window.innerHeight * 1, {
            smooth: "easeInQuad",
            duration: 2000,
        });
    }
    return <div className={"return-to-surface"} onClick={returnUp} />
}